@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

.cart-summary-section {
    .cart__blocks {
        margin-bottom: 30px;
    }
	.cart-summary-title h5 {
		font-weight: 500;
        margin-bottom: 10px;
        line-height: 1.1em;
        font-size: var(--heading-5-fs);
	}
    .cart-summary {
        border-radius: var(--border-radius);
        border: 2px solid var(--secondary-color);
        p {
            margin: 0;
        }
    }
    .cart-subtotal-block {
        >div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 15px 20px;
            border-bottom: 1px solid var(--miscellaneous-color);
        }
        h6 {
            font-weight: 600;
            font-size: 14px;
        }
    }
    .tax-note {
        display: block;
        padding: 15px 20px;
        border-bottom: 1px solid var(--miscellaneous-color);
        font-size: var(--heading-6-fs);
        font-size: 14px;
    }
    .cart-total-price {
        font-size: 25px;
        font-weight: 600;
        color: var(--primary-color);
    }
    p.cart-subtotal {
        font-size: 15px;
        font-weight: 500;
    }   
    .cart-checkout {
        padding: 20px;
        .btn {
            width: 100%;
        }
    }
    cart-note {
        label {
            margin-bottom: 10px;
            font-size: var(--heading-5-fs);
            font-weight: 500;
        }
        textarea {
            padding: 10px 15px;
            border-radius: var(--border-radius);
            border: 1px solid var(--miscellaneous-color);
            max-width: 100%;
        }
    }
    .additional-checkout-buttons {
        padding: 0 20px 20px;
        .dynamic-checkout__content {
            .shopify-cleanslate ._1M9S34W-UyhhDRRQQiV3RH {
                height: 46px !important; 
                min-width: 100px;
            }
        }
    }
    @media (min-width: 750px) {
        .section-container {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -10px;
        }
        .cart__blocks {
            padding: 0 10px;
        }
        .cart-block-summary, .cart-block-note {
            flex: 1;
            min-width: 350px;
        }
        cart-note {
            textarea {
                height: 251px;
            }
        }
    }
    @media (min-width: 1300px) {
        flex: 0 0 400px;
        .cart-block-summary, .cart-block-note {
            flex: 0 0 400px;
            max-width: 400px;
        }
        .section-container {
            display: block;
            margin: 0;
        }
        .cart__blocks {
            padding: 0;
        }
        cart-note {
            textarea {
                height: 120px;
            }
        }
    }
}